import { Gender, Anthroponym } from "shevchenko";
import * as shevchenko from "shevchenko";

/**
 * Decline ukrainian anthroponym.
 * @customfunction
 * @param {string} grammaticalCase of the following: genitive, dative, accusative, ablative, locative, vocative
 * @param {string} gender of the following: male, female
 * @param {string} lastName of the following: genitive, dative, accusative, ablative, locative, vocative
 * @param {string} firstName of the following: genitive, dative, accusative, ablative, locative, vocative
 * @param {string} middleName of the following: genitive, dative, accusative, ablative, locative, vocative
 * @returns The sum of the two numbers.
 */
export function decline(
  grammaticalCase: string,
  gender: string,
  lastName: string,
  firstName: string,
  middleName: string
): string {
  const anthroponym = {
    gender: gender == "female" ? Gender.Female : Gender.Male,
    firstName: firstName,
    middleName: middleName,
    lastName: lastName,
  };

  return declineAnthroponym(grammaticalCase, anthroponym);
}

export function declineFullname(grammaticalCase: string, gender: string, fullName: string): string {
  const fullNameParts = fullName.replace(/\s\s+/g, " ").split(" ");
  const anthroponym = {
    gender: gender == "female" ? Gender.Female : Gender.Male,
    firstName: fullNameParts[0],
    middleName: fullNameParts[1],
    lastName: fullNameParts[2],
  };

  return declineAnthroponym(grammaticalCase, anthroponym);
}

function anthroponymToString(anthroponym: Anthroponym): string {
  return `${anthroponym.lastName} ${anthroponym.firstName} ${anthroponym.middleName}`;
}

function declineAnthroponym(grammaticalCase: string, anthroponym: Anthroponym) {
  switch (grammaticalCase) {
    case "genitive":
      return anthroponymToString(shevchenko.inGenitive(anthroponym));
    case "dative":
      return anthroponymToString(shevchenko.inDative(anthroponym));
    case "accusative":
      return anthroponymToString(shevchenko.inAccusative(anthroponym));
    case "ablative":
      return anthroponymToString(shevchenko.inAblative(anthroponym));
    case "locative":
      return anthroponymToString(shevchenko.inLocative(anthroponym));
    case "vocative":
      return anthroponymToString(shevchenko.inVocative(anthroponym));
  }
}

CustomFunctions.associate("DECLINE", decline);